export const updateProfile = async (prevState, payload) => {
  const {
    formData,
    cover
  } = payload;
  const fields = Object.fromEntries(formData);
  const filteredFields = Object.fromEntries(Object.entries(fields).filter(([_, value]) => value !== ""));
  console.log(formData);
  const Profile = z.object({
    cover: z.string().optional(),
    name: z.string().max(60).optional(),
    surname: z.string().max(60).optional(),
    description: z.string().max(255).optional(),
    city: z.string().max(60).optional(),
    school: z.string().max(60).optional(),
    work: z.string().max(60).optional(),
    website: z.string().max(60).optional()
  });
  const validatedFields = Profile.safeParse({
    cover,
    ...filteredFields
  });
  console.log(validatedFields);
  if (!validatedFields.success) {
    console.log(validatedFields.error.flatten().fieldErrors);
    return {
      succes: false,
      error: true
    };
  }
  const {
    userId
  } = auth();
  console.log(userId);
  if (!userId) {
    return {
      succes: false,
      error: true
    };
  }
  try {
    await prisma.user.update({
      where: {
        id: userId
      },
      data: validatedFields.data
    });
    return {
      succes: true,
      error: false
    };
  } catch (err) {
    console.log(err);
    return {
      succes: false,
      error: true
    };
  }
};
export const switchLike = async postId => {
  const {
    userId
  } = auth();
  if (!userId) throw new Error("User is not authenticates!");
  try {
    const existingLike = await prisma.like.findFirst({
      where: {
        postId,
        userId
      }
    });
    if (existingLike) await prisma.like.delete({
      where: {
        id: existingLike.id
      }
    });else {
      await prisma.like.create({
        data: {
          postId,
          userId
        }
      });
    }
  } catch (err) {
    console.log(err);
    throw new Error("Something went wrong !");
  }
};
export const addComment = async (postId, desc) => {
  const {
    userId
  } = auth();
  if (!userId) throw new Error("User is not authenticates!");
  try {
    const createdComment = await prisma.comment.create({
      data: {
        desc,
        userId,
        postId
      },
      include: {
        user: true
      }
    });
    return createdComment;
  } catch (err) {
    console.log(err);
    throw new Error("Somenthing went wrong !");
  }
};
export const addPost = async (formData, img) => {
  const desc = formData.get("desc");
  const Desc = z.string().min(1).max(255);
  const validatedDesc = Desc.safeParse(desc);
  if (!validatedDesc.success) {
    //TODO
    console.log("description is not valid");
    return;
  }
  const {
    userId
  } = auth();
  if (!userId) throw new Error("User is not authenticated!");
  try {
    await prisma.post.create({
      data: {
        desc: validatedDesc.data,
        userId,
        img
      }
    });
    revalidatePath("/");
  } catch (err) {
    console.log(err);
  }
};
export const addStory = async img => {
  const {
    userId
  } = auth();
  if (!userId) throw new Error("User is not authenticated!");
  try {
    const existingStory = await prisma.story.findFirst({
      where: {
        userId
      }
    });
    if (existingStory) {
      await prisma.story.delete({
        where: {
          id: existingStory.id
        }
      });
    }
    const createdStory = await prisma.story.create({
      data: {
        userId,
        img,
        expiredAt: new Date(Date.now() + 24 * 60 * 60 * 1000)
      },
      include: {
        user: true
      }
    });
    return createdStory;
  } catch (err) {
    console.log(err);
  }
};